
body {
  margin: 0;
}
#shop {
  position: relative;
}
#header {
  height: 60px;
  border-bottom: 1px solid #f8f8f8;
  background-color: white;
  position: relative;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.hairlines #header {
  border-bottom: 0.5px solid #f8f8f8;
}
#header__logo {
  width: 81px;
  height: 20px;
  position: absolute;
  top: 50%;
  left: 12px;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}
#header__toggle {
  width: 22px;
  height: 22px;
  position: absolute;
  top: 50%;
  right: 12px;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}
#shelf {
  position: absolute;
  top: 60px;
  left: 0;
  right: 0;
  bottom: 50px;
  overflow: hidden;
}
.categories {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  overflow: scroll;
  overflow: -moz-scrollbars-none;
  -ms-overflow-style: none;
}
.categories::-webkit-scrollbar {
  width: 0 !important;
}
.categories__item {
  width: 100px;
  height: 50px;
  background-color: #f8f8f8;
  line-height: 50px;
  padding-left: 12px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-size: 13px;
  color: #555555;
}
.categories__item--selected {
  background-color: white;
}
.goods {
  position: absolute;
  left: 100px;
  right: 0;
  top: 0;
  bottom: 0;
  overflow: scroll;
  overflow: -moz-scrollbars-none;
  -ms-overflow-style: none;
}
.goods::-webkit-scrollbar {
  width: 0 !important;
}
.goods__item {
  position: relative;
  height: 100px;
}
.goods__item__pic {
  position: absolute;
  width: 69px;
  height: 69px;
  top: 15px;
  left: 15px;
}
.goods__item__name {
  color: #36383a;
  position: absolute;
  left: 89px;
  top: 16px;
  font-size: 13px;
  line-height: 13px;
}
.goods__item__monthly-sales {
  position: absolute;
  left: 89px;
  top: 45px;
  font-size: 10px;
  line-height: 10px;
  color: #555555;
}
.goods__item__price {
  position: absolute;
  left: 89px;
  top: 68px;
  font-size: 13px;
  line-height: 13px;
  color: #2c4d99;
  font-weight: bold;
}
.goods__item__price__unit {
  font-size: 9px;
}
.goods__item__original-price {
  position: absolute;
  left: 124px;
  top: 71px;
  font-size: 9px;
  line-height: 9px;
  color: #bfbfbf;
  text-decoration: line-through;
}
.goods__item__original-price {
  position: absolute;
  left: 124px;
  top: 71px;
  font-size: 9px;
  line-height: 9px;
  color: #bfbfbf;
  text-decoration: line-through;
}
.goods__item__operate {
  position: absolute;
  right: 12px;
  top: 67px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.goods__item__operate__minus {
  width: 16px;
  height: 16px;
  vertical-align: bottom;
}
.goods__item__operate__count {
  font-size: 12px;
  line-height: 16px;
  height: 16px;
  width: 35px;
  text-align: center;
  display: inline-block;
}
.goods__item__operate__plus {
  width: 16px;
  height: 16px;
  vertical-align: bottom;
}
#footer {
  position: absolute;
  height: 50px;
  bottom: 0;
  left: 0;
  right: 0;
  border-top: 1px solid #f5f5f5;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.hairlines #footer {
  border-top: 0.5px solid #f5f5f5;
}
#footer__shopping-cart {
  position: absolute;
  top: 50%;
  width: 25px;
  height: 25px;
  left: 16px;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}
#shopping-cart {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 50px;
  top: 0;
}
#shopping-cart__cover {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: black;
  opacity: 0.58;
}
#shopping-cart__list {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 220px;
  background: white;
}
#shopping-cart__list__header {
  position: relative;
  height: 43px;
  background: #f9f9f9;
}
#shopping-cart__list__header__tips {
  position: absolute;
  top: 50%;
  left: 12px;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  color: #818181;
  font-size: 15px;
  line-height: 15px;
}
#shopping-cart__list__header__delete-wrapper {
  float: right;
  height: 43px;
  line-height: 43px;
  margin-right: 12px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
#shopping-cart__list__header__delete {
  width: 15px;
  height: 15px;
  margin-right: 12px;
  display: inline-block;
  vertical-align: middle;
}
#shopping-cart__list__header__delete-tips {
  font-size: 12px;
  color: #818181;
  display: inline-block;
  vertical-align: middle;
}
.shopping-cart__list__item {
  height: 47px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.shopping-cart__list__item__name {
  -webkit-box-flex: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
  margin-left: 12px;
  font-size: 13px;
  color: #555555;
}
.shopping-cart__list__item__operate {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-right: 12px;
}
.shopping-cart__list__item__operate__minus {
  width: 20px;
  height: 20px;
}
.shopping-cart__list__item__operate__count {
  width: 45px;
  line-height: 20px;
  height: 20px;
  font-size: 13px;
  display: inline-block;
  text-align: center;
  color: #555555;
}
.shopping-cart__list__item__operate__plus {
  width: 20px;
  height: 20px;
}
#footer__badge {
  position: absolute;
  left: 42px;
  top: 7px;
  padding: 0 3px;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  border-radius: 7px;
  border-width: 7px;
  background: #2c4d99;
  display: inline-block;
  height: 15px;
  min-width: 15px;
  text-align: center;
  font-size: 10px;
  line-height: 15px;
  color: white;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
#footer__amount {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  right: 145.5px;
  font-size: 15px;
  color: #2c4d99;
  font-weight: bold;
}
#footer__amount__unit {
  font-size: 11px;
}
#footer__submit {
  position: absolute;
  right: 0;
  width: 125px;
  height: 50px;
  top: 0;
  background: #2c4d99;
  border: none;
  color: white;
  font-size: 17px;
}